body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  margin: 0;
  padding: 0;
  font-size: 16px;
  flex-direction: column; }
  .App header {
    display: flex;
    justify-content: center; }

.App,
.checkbox,
.form-inline,
.form-group {
  display: flex;
  flex-wrap: wrap; }

.form-inline {
  flex-direction: column;
  justify-content: center;
  flex-basis: 100%; }

@media (min-width: 768px) {
  .form-inline {
    flex-direction: row; }
  .form-control {
    min-width: 12rem; } }

.form-group {
  justify-content: center; }

.form-control {
  padding: 0.5rem 0.25rem;
  margin: 0.25rem 0.5rem;
  border-radius: 0.25rem;
  flex-basis: 100%; }

.button {
  padding: 0.5rem 0.75rem;
  margin: 0.25rem;
  border-radius: 0.25rem;
  opacity: 0.8;
  text-align: center;
  line-height: 1.5;
  font-size: 1rem;
  display: block;
  color: #fff;
  font-weight: 400;
  border: 1px solid #01579b;
  box-shadow: 0 0.7px 0.5px 0.7px rgba(2, 119, 189, 0.8);
  flex-basis: 95%; }

.button--primary {
  background-color: #0277bd; }

table {
  border-collapse: collapse; }

table td,
table th {
  padding: 0.5rem;
  margin: 0.25rem;
  border: 0.25rem solid #fff;
  table-layout: fixed;
  padding: 0.5rem; }

table th {
  background-color: #bdbdbd; }

table tbody td:last-child {
  color: #d32f2f;
  padding: 0; }

table tbody tr:nth-child(even) {
  background-color: #e0e0e0; }

.icons {
  display: flex;
  justify-content: space-evenly;
  margin: 0; }
  .icons .fa-edit {
    color: #0277bd; }
  .icons .fa-trash-alt {
    color: #d32f2f; }

.checkbox {
  display: block;
  position: relative;
  padding-left: 2rem;
  margin: -0.75rem auto 0.75rem 1rem;
  cursor: pointer;
  font-size: 1.35rem;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  -webkit-transform: scale(0.8);
          transform: scale(0.8); }

.checkbox input {
  position: absolute;
  opacity: 0;
  cursor: pointer;
  height: 0;
  width: 0; }

.check {
  position: absolute;
  top: 0;
  left: 0;
  height: 1.5rem;
  width: 1.5rem;
  background-color: #eee;
  border: 0.1rem solid #0277bd;
  border-radius: 0.25rem; }

.checkbox:hover input ~ .check {
  background-color: #ccc; }

.checkbox input:checked ~ .check {
  background-color: #0277bd; }

.check:after {
  content: "";
  position: absolute;
  display: none; }

.checkbox input:checked ~ .check:after {
  display: block; }

.checkbox .check:after {
  left: 0.5rem;
  top: 0.25rem;
  width: 0.25rem;
  height: 0.5rem;
  border: solid #fff;
  border-width: 0 0.15rem 0.15rem 0;
  -webkit-transform: rotate(45deg);
  transform: rotate(45deg); }

.App--sem-atividades {
  width: 95vw;
  height: 20rem;
  border: 1px solid;
  align-self: center;
  box-shadow: 1px 2px 1px 1px rgba(2, 119, 189, 0.8);
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1.5rem;
  text-shadow: 0 1px 1px rgba(2, 119, 189, 0.8);
  text-align: center; }

table tbody tr.completed {
  opacity: 0.7; }
  table tbody tr.completed td:nth-child(-n + 3) {
    text-decoration: line-through; }

hr {
  display: none; }

hr.completed {
  display: block;
  border: 1px solid;
  width: 60vw;
  position: absolute;
  z-index: -1;
  padding: 0;
  margin: 0;
  margin-top: -0.75rem; }

